.daterange-container {
  position: relative; }
  .daterange-container .icon-calendar {
    position: absolute;
    right: 10px;
    font-size: 15px;
    cursor: pointer; }

.daterange-label {
  cursor: pointer; }

.rdr-Show {
  display: block; }

.rdr-Hide {
  display: none; }

.rdr-DateRange {
  box-sizing: border-box;
  white-space: nowrap;
  border-radius: 2px;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.25) 0 0 1em;
  position: absolute;
  z-index: 1;
  top: 28px; }
  .rdr-DateRange .rdr-Calendar {
    box-shadow: none; }

.rdr-Calendar {
  display: inline-block;
  box-sizing: border-box;
  width: 280px;
  padding: 10px;
  white-space: normal;
  letter-spacing: 0;
  color: #000000;
  border-radius: 2px;
  background: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.25) 0 0 1em; }

.rdr-MonthAndYear-innerWrapper {
  font-size: 12px;
  line-height: 18px;
  box-sizing: border-box;
  height: 40px;
  padding: 5px 0;
  text-align: center; }

.rdr-MonthAndYear-button {
  display: block;
  box-sizing: border-box;
  width: 25px;
  height: 25px;
  margin: 0 10px;
  padding: 0;
  border: 0;
  outline: none;
  background: #bdc3c7;
  box-shadow: none;
  background: #ffffff;
  cursor: pointer; }
  .rdr-MonthAndYear-button:hover {
    background: #eeeeee; }
  .rdr-MonthAndYear-button i {
    display: block;
    width: 0;
    height: 0;
    padding: 0;
    text-align: center;
    border-style: solid; }
  .rdr-MonthAndYear-button.prev {
    float: left; }
    .rdr-MonthAndYear-button.prev i {
      margin: 0 0 0 3px;
      border-width: 5px 7px 5px 5px;
      border-color: transparent #34495e transparent transparent; }
  .rdr-MonthAndYear-button.next {
    float: right; }
    .rdr-MonthAndYear-button.next i {
      margin: 0 0 0 10px;
      border-width: 5px 5px 5px 7px;
      border-color: transparent transparent transparent #34495e; }
  .rdr-MonthAndYear-button:disabled {
    background: transparent !important; }
    .rdr-MonthAndYear-button:disabled.next i {
      border-left-color: #d2d8dc; }
    .rdr-MonthAndYear-button:disabled.prev i {
      border-right-color: #d2d8dc; }

.rdr-MonthAndYearSelect {
  height: 25px;
  width: 65px; }

.rdr-WeekDay {
  font-size: 12px;
  font-weight: 600;
  line-height: 18.07143px;
  display: inline-block;
  box-sizing: border-box;
  width: 36.14286px;
  height: 18.07143px;
  margin-bottom: 1px;
  text-align: center;
  letter-spacing: initial; }

.rdr-Day {
  font-size: 12px;
  line-height: 36.14286px;
  display: inline-block;
  box-sizing: border-box;
  width: 36.14286px;
  height: 36.14286px;
  cursor: pointer;
  transition: transform 0.1s ease;
  text-align: center;
  letter-spacing: initial; }
  .rdr-Day.is-passive {
    cursor: default;
    opacity: 0.4; }
  .rdr-Day.is-inRange {
    background: rgba(78, 184, 94, 0.2); }
  .rdr-Day.is-selected {
    color: #ffffff;
    background: #4eb85e; }
  .rdr-Day:hover:not(.is-selected) {
    background: rgba(78, 184, 94, 0.4); }

.rdr-PredefinedRanges {
  width: 140px;
  display: inline-block;
  vertical-align: top;
  margin-left: 10px;
  margin-top: 10px; }
  .rdr-PredefinedRangesItem {
    display: block;
    font-size: 12px;
    color: #2c3e50;
    padding: 6px 14px;
    border-radius: 2px;
    text-decoration: none;
    margin-bottom: 3px; }
    .rdr-PredefinedRangesItemActive, .rdr-PredefinedRangesItem:hover {
      background: #4c6375;
      color: #fff; }

.rdr-Button {
  border: none;
  border-radius: 2px;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  height: 25px;
  line-height: 25px;
  text-align: center;
  letter-spacing: 0.5px;
  display: inline-block;
  outline: 0;
  vertical-align: middle;
  width: 48%;
  color: #fff;
  cursor: pointer; }
  .rdr-Button:hover {
    opacity: 0.8; }

.rdr-ApplyButton {
  margin-right: calc(4%);
  background-color: #f05b1f; }

.rdr-CancelButton {
  background-color: #757985; }

.range-button-group {
  margin-top: 5px; }
